.myButton2 {
	
    background-color:#163948;
    height:40px;
    
	border-radius:5px;
	border:1px solid #d6bcd6;
	display:inline-block;
	cursor:pointer;
	color:white;
	font-family:"montserrat",sans-serif;
	font-size:1rem;
	padding:6px 15px;
	text-decoration:none;
    text-shadow:0px 1px 0px #e1e2ed;
    margin-top: 5%;
    font-weight:400;
}
.myButton2:hover {

	background-color:#DDADA6;
	color: white;
	text-decoration: none;
}
.myButton2:active {
	position:relative;
	top:1px;
}
