.d {
	box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.2);
	padding: 16px;
	text-align: center;
	background-color: #f1f1f1;
	margin-left : 10%;
	margin-right: 10%;
	margin-top : 2%;
	
}
.m{
	background-color:white;
	margin-left : 10%;
	margin-right: 10%;
	margin-top : 5%;
	
}
.pah {
	color:#163948;
	font-size:large;
    padding-top: 5%;
    font-weight: 600;
}
.pa {
	color:#163948;
	font-size:large;
    padding-top: 3%;
    font-weight: 600;
}
.unclicked_button{
    width:50%;
   
    font-size: large;
    border :1px solid #163948;
	background-color: white;
	padding:10px 40px;
	margin:5%;
	padding-left: 15%;
	padding-right: 15%;
    border-radius: 5px;
    margin-top: 6%;
    margin-bottom: 4%;
}
.unclicked_button:hover {
	background:linear-gradient(to bottom, #BCD8DF 5%);
	background-color:#BCD8DF ;
}

.clicked_button{
    width:50%;
   
    font-size: large;
    border :1px solid #163948;
	background-color: #BCD8DF;
	padding:10px 40px;
	margin:5%;
	padding-left: 15%;
	padding-right: 15%;
    border-radius: 5px;
    margin-top: 6%;
    margin-bottom: 4%;
    
}

.left_button{
    width:50%;
    background-color:#163948;
    color:white;
    text-align: left;
    padding-top: 1.5%;
    padding-bottom: 1.5%;
    padding-left: 5%;
    margin-top: 5%;
    font-size: 1.5rem;
}

.right_button{
    width:50%;
    background-color:#163948;
    color:white;
    text-align: right;
    padding-top: 1.5%;
    padding-bottom: 1.5%;
    padding-right: 5%;
    margin-top: 5%;
    font-size: 1.5rem;
}

.start_button{
    width:100%;
    background-color:#163948;
    color:white;
    text-align: right;
    padding-top: 1.5%;
    padding-bottom: 1.5%;
    padding-right: 5%;
    margin-top: 5%;
    font-size: 1.5rem;
}
.endpage{
    width:100%;
    background-color:#163948;
    color:white;
    text-align: left;
    padding-top: 1.5%;
    padding-bottom: 1.5%;
    padding-left: 5%;
    margin-top: 5%;
    font-size: 1.5rem;
}
.right_buttonl{
    width:50%;
    background-color:#163948;
    color:white;
    text-align: right;
    padding-top: 1.5%;
    padding-bottom: 1.5%;
    padding-right: 5%;
    margin-top: 5%;
    font-size: 1.5rem;
    text-decoration: none;
}
.right_buttonl:hover{
    color: white;
    text-decoration: none;
}
.intropara{
    text-align: justify;
    padding-left: 10%;
    padding-right: 10%;
    font-size: larger;
}
.nav_buttons1{
    border-radius: 50%;
    margin-right: 2%;
    margin-left: 2%;
    background-color: #163948;
    color: white;
}
.nav_buttons1:hover{
    
    background:linear-gradient(to bottom, #f1f1f1 5%);
    background-color:#f1f1f1 ;
    color: #163948;
}
.nav_buttons2{
    border-radius: 50%;
    margin-right: 2%;
    margin-left: 2%;
    background-color: #cf1b1b;
    color: white;
}
.nav_buttons2:hover{
    
    background:linear-gradient(to bottom, #f1f1f1 5%);
    background-color:#f1f1f1 ;
    color: #163948;
}
.nav_buttons3{
    border-radius: 50%;
    margin-right: 2%;
    margin-left: 2%;
    background-color: #c4fb6d;
    color: #163948;
}
.nav_buttons3:hover{
    
    background:linear-gradient(to bottom, #f1f1f1 5%);
    background-color:#f1f1f1 ;
    color: #163948;
}
#button10{
    
    padding-top: 0.1cm;
    padding-bottom: 0.1cm;
    padding-left: 0.2cm;
    padding-right: 0.2cm;
}
#buttonr{
    padding-top: 0.1cm;
    padding-bottom: 0.1cm;
    padding-left: 0.3cm;
    padding-right: 0.3cm;
}

.resultheading{
    text-align: center;
    padding-top: 3%;
    padding-bottom: 5%;
    font-family: "montserrat",sans-serif;
    font-size: 35px;

}
#resultpara{
    padding-left: 10%;
    padding-right: 10%;
    font-size: large;
}



@media screen and (max-width:450px){
    .d{
        margin-left: 2%;
        margin-right: 2%;
    }
    .m{
        margin-left: 2%;
        margin-right: 2%;
    }
    #buttonr{
    margin-right: 5%;
    margin-bottom: 5%;
    }
}